import React, { Component } from 'react'
import Header from '../components/Header'
import { Link } from 'react-router-dom'
import Loading from '../components/Loading'
import { env } from '../settings'

class Homepage extends Component {

    constructor(props) {
      super(props)
      this.state = {
        channelData: [],
        loading: true,
        city: "home",
        url: "/home",
        channelInfo: [{
          categoryTitle: ""
        }]
      }
    }
    loadData = () => {
      this.setState({ loading: true })
      const loadURL = (env === "huba") ? "https://huba.sk/index.php/API/home" : "/API/home/index.json"
      fetch(loadURL)
        //		fetch("/API/"+ this.props.match.params.channel + "/index.json")
        .then(results => {
          return results.json();
        })
        .then(data => {
          const channelData = data.channelData.entries;
          const channelInfo = {
            categoryTitle: data.channelData.categoryTitle
          };
          this.setState({ channelData, channelInfo });
        })
      this.setState({ loading: false, city: "home", url: "/home" })
      console.log('Homepage loadData')
    }


    componentDidMount() {
      document.title = "Komunálne voľby 2018"
      this.loadData("home")
    }
    render() {
      return (
        <div className="flex">
          <Header title="?018" {...this.props} />
          <div className={"home bg"}>
            <div className="articles page-container container">
              {this.state.loading && <Loading />}
              {this.state.channelData.map((channelEntry) => {
                if (channelEntry.url_title === "kviz-odpovede") {
                  return null
                }
                return (
                  <div key={channelEntry.url_title} className="channelEntry">
                    <h2>{channelEntry.cislo > 0 && <span className="cislo">{channelEntry.cislo}</span>}{channelEntry.url_title ? <Link to={"/home/" + channelEntry.url_title}>{channelEntry.title}</Link> : channelEntry.title}</h2>
                    <p className={channelEntry.cislo > 0 ? "space small" : ""}>{channelEntry.summary}</p>
                    {channelEntry.images && channelEntry.images.map(
                      (image, key) => {
                        return (
                          <div key={key}>
                            {image.location ?
                              <a href={image.location} className="noLine">
                                <img src={image.image} alt={image.desc} />
                              </a>
                              :
                              <img src={image.image} alt={image.desc} />
                            }
                          </div>
                        )
                      }
                    )}
                  </div>
                )
              }
              )}
              <div className="channelEntry">

              </div>

            </div>
          </div>
        </div>

      );
    }
  }

export default Homepage;



